import React from 'react'
import { Input, Divider, Tag, Select, Button } from 'antd'
import classnames from 'classnames';
import './index.less'
import { workStatus, workLevel } from 'constants/status_constant'
import { circleStatus } from 'components/StateLess';
const { Option } = Select;
const { moment } = window.GLOBAL_PARAMS

/*  modal-form-columns  */
export function setWorkModalForms({
  currentMain, selectList, description, deviceList, customerList
}) {
  let columns =
    [
      {
        key: 'workNo',
        label: '',
        value: <Input disabled placeholder='' style={{ width: '330px' }} />,
        getFieldDecorator: {
          initialValue: currentMain.workNo
        }
      },
      {
        key: 'typeId',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear disabled
            style={{ width: 330, }}
          >
            {
              selectList && selectList.map(item => {
                return <Option key={item.id} value={item.id}>
                  {item.typeName}
                </Option>
              })
            }
          </Select>
        ),
        getFieldDecorator: {
          initialValue: currentMain.typeId
        }
      },
      {
        key: 'deviceName',
        label: '',
        value: (
          <Input disabled style={{ width: 330 }} />
        ),
        getFieldDecorator: {
          initialValue: currentMain.deviceId
        }
      },
      {
        key: 'customerId',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear
            style={{ width: 330, }} disabled
          >
            {
              customerList && customerList.map(item => {
                return <Option key={item.id} value={item.id}>
                  {item.customerName}
                </Option>
              })
            }
          </Select>
        ),
        getFieldDecorator: {
          initialValue: currentMain.customerId
        }
      },
      {
        key: 'workType',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear disabled
            style={{ width: 330, }}
          >
            <Option value={1}>
              
            </Option>
            <Option value={2}>
              
            </Option>
            <Option value={3}>
              
            </Option>
            <Option value={4}>
              
            </Option>
          </Select>
        ),
        getFieldDecorator: {
          initialValue: currentMain.workType
        }
      },
      {
        key: 'workStatus',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear disabled
            style={{ width: 330, }}
          >
            {
              Object.keys(workStatus) && Object.keys(workStatus).map(item => {
                return <Option key={item} value={item}>
                  {workStatus[item]}
                </Option>
              })
            }
          </Select>
        ),
        getFieldDecorator: {
          initialValue: currentMain.workStatus
        }
      },
      {
        key: 'level',
        label: '',
        value: (
          <Select
            optionFilterProp='children'
            showSearch allowClear disabled
            style={{ width: 330, }}
          >
            {
              Object.keys(workLevel) && Object.keys(workLevel).map(item => {
                return <Option key={item} value={item}>
                  {workLevel[item]}
                </Option>
              })
            }
          </Select>
        ),
        getFieldDecorator: {
          initialValue: currentMain.typeId
        }
      },
      {
        key: 'expecteTime',
        label: '',
        value: (
          <Input disabled style={{ width: 330 }} />
        ),
        getFieldDecorator: {
          initialValue: currentMain.expecteTime
        }
      },
      {
        key: 'description',
        label: '',
        value: (<React.Fragment>
          {description()}
        </React.Fragment>
        ),
      },

    ]
  return columns;
}
/*   table-columns */
export function setWorkColumns(
  { cols, handleDistribute, handleDetail, handleReceive, handleOver, handleRecall, handleSubmit, handleRefuse }
) {
  const userRole = {
    admin: true,
    repair: true
  };
  let columns = []
  cols.map(col=>{
    let item = { title:col.titleValue,dataIndex:col.titleKey,key:col.titleKey,render:value=>value||'-'}
    if(col.titleKey === 'maintainType'){
      item.render = value => value ? <span className='work_table'>
        <span
          className={
            classnames({
              red: value === 4,
              green: value !== 4,
              circle: true
            })
          }
        ></span>
        {value === 1 ? '' : value === 2 ? '' : value === 3 ? '' : value === 4 ? '' : ''}
      </span> : '-'
    }

    if(col.titleKey === 'level'){
      item.render = value => {
        return <span>
          {value === 1 && <Tag color="magenta"></Tag>}
          {value === 3 && <Tag color="blue"></Tag>}
          {value === 2 && <Tag color="gold"></Tag>}
        </span>
      }
    }

    if(col.titleKey === 'workStatus'){
      item.render = value => circleStatus(value, 'work')
    }

    if(col.titleKey === 'expecteTime'){
      item.render = value => value ? moment(value).format('YYYY-MM-DD') : '-'
    }

    columns.push(item)
  })
  if ((userRole.admin && userRole.repair) || userRole.admin || userRole.repair) {
    let isSuper = userRole.admin && userRole.repair, isAdmin = userRole.admin, isRepair = userRole.repair;
    columns = columns.concat([
      {
        title: '',
        dataIndex: 'id',
        key: 'id', align: 'right',
        render: (value, record) => {
          const btn1 = <span onClick={() => handleDetail(record)}></span>;
          const btn2 = <span onClick={() => handleDistribute(record)}></span>;
          const btn3 = <span onClick={() => handleReceive(record, 'receive')}></span>;
          const btn7 = <span onClick={() => handleRefuse(record, 'refuse')}></span>;
          const btn4 = <span onClick={() => handleOver(record, 'over')}></span>;
          const btn5 = <span onClick={() => handleRecall(record)}></span>;
          const btn6 = <span onClick={() => handleSubmit(record, 'submit')}></span>;
          return <span style={{ color: '#6236FF', cursor: 'pointer' }}>
            {btn1}
            {/*  */}
            {
              (isSuper || isAdmin) && [0].includes(record.workStatus) && [
                <Divider type='vertical' />, btn2
              ]
            }
            {/*  */}
            {
              (isSuper || isRepair) && [1].includes(record.workStatus) && [
                <Divider type='vertical' />,
                btn3,
                <Divider type='vertical' />,
                btn7
              ]
            }
            {/*  &&  */}
            {
              (isSuper || isAdmin) && [3, 6].includes(record.workStatus) && [
                <Divider type='vertical' />,
                btn5
              ]
            }
            {/*  */}
            {
              (isSuper || isRepair) && [4].includes(record.workStatus) && [
                <Divider type='vertical' />,
                btn6
              ]
            }
            {/*  */}
            {
              (isSuper || isAdmin) && [5].includes(record.workStatus) && [
                <Divider type='vertical' />,
                btn4
              ]
            }
          </span>
        }
      }
    ])
  }
  return columns;
}